
import { Box, Heading } from '@chakra-ui/react';
import React from 'react'
import Typewriter from "typewriter-effect";

const Typeabout = () => {

  return (
    
      <Heading  color='#fb982f' fontSize={"30px"} fontStyle="italic" >
        <Typewriter    
        options={{
            strings:['Rashmi Gautam', ' a Full Stack web Developer '],
            autoStart: true,
            loop: true,
            deleteSpeed: 100,  
            delay:500, 
          
        }}
        />
        </Heading>
        
   
  )
}

export default Typeabout