import { Heading } from '@chakra-ui/react'
import React from 'react'

const Contact = () => {
  return (
    <div id='contact'>
       <Heading as="h1" size="xl" color={"rgb(199, 121, 203)"} pt={40}>
        <i class="fa fa-chevron-left" aria-hidden="true"></i> Contact
        <span> / </span>
        <i class="fa fa-chevron-right" aria-hidden="true"></i>
      </Heading>

    </div>
  )
}

export default Contact