import React from "react";
import "../style/about.css";
import AOS from "aos";
import "aos/dist/aos.css";
import {
  Box,
  Center,
  Flex,
  Heading,
  Image,
  SimpleGrid,
} from "@chakra-ui/react";
import Typeabout from "./TypeAbout";
AOS.init();

const About = () => {
  return (
    <Box
      id="about"
      margin={"auto"}
      paddingTop={{ base: "70px", sm: "70px", md: "100px", lg: "100px" }}
    >
      <Heading as="h1" size="xl" color={"blue.300"}>
        <i class="fa fa-chevron-left" aria-hidden="true"></i> About
        <span> / </span>
        <i class="fa fa-chevron-right" aria-hidden="true"></i>
      </Heading>

      <Box pt={40} width="40%" margin={"auto"}>
        <Heading as="h1" size="md" color={"blue.300"}>
          I'm{" "}
        </Heading>
        <Typeabout />

        <Heading as="h1" pt={7} lineHeight={7} color={"blue.300"} size="md">
          <i class="fa fa-pencil" aria-hidden="true"></i> An ambitious,
          self-motivated, and inspiring full-stack web developer with
          considerable technical skills who possesses self-discipline, and a
          growth mindset.
        </Heading>
        <Heading as="h1" pt={7} lineHeight={7} color={"blue.300"} size="md">
          <i class="fa fa-pencil" aria-hidden="true"></i> I am persuing
          Full-Stack Web Development Course from Masai School
        </Heading>
     </Box>
     
      <Flex  justifyContent="center" width="40%" margin={"auto"} columns={[2, 2, 3]} pt="60px" gap={10} >
          <a href="https://www.linkedin.com/in/rashmi-gautam16/" target={"_blank"}>
            <Flex className="icon" fontSize={"33px"} alignItems={"center"} justifyContent="center" width={"60px"} height="60px" borderRadius={"50%"} bg="blue.600">
            <i id="link" class="fa fa-linkedin" aria-hidden="true"></i>
            </Flex>
          </a>
          <a href="rashmigaut99@gmail.com" target={"_blank"}>
            <Flex className="icon"  fontSize={"30px"} alignItems={"center"} justifyContent="center" width={"60px"} height="60px" borderRadius={"50%"} bg="red.500">
            <i id="email" class="fa fa-envelope" aria-hidden="true"></i>
            </Flex>
          </a>
          <a href="https://github.com/Rashmigautam16" target={"_blank"}>
            <Flex className="icon" fontSize={"37px"} alignItems={"center"} justifyContent="center" width={"60px"} height="60px" borderRadius={"50%"} bg="black">
            <i id="github" class="fa fa-github" aria-hidden="true"></i>
            </Flex>
          </a>
        </Flex>
    
    </Box>
  );
};

export default About;
